









































































import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import EventBus from '@/utils/eventBus'
import { forEach, trim, debounce } from 'lodash'
import getRepository from '@/services'
import { KycRepository } from '@/services/repositories/kyc'
const apiKyc: KycRepository = getRepository('kyc')

import countryJson from '@/utils/country/index.json'
interface IListCountry {
  name: string
  dialCode: string
  isoCode: string
  flag: string
}
@Component
export default class KycFilter extends Vue {
  filter = {
    search: '',
    orderBy: 1,
    fromCreatedAt: '',
    toCreatedAt: '',
    nationality: '',
    identificationType: '',
    approvedBy: ''
  }
  loading = false
  queryApprove = {
    page: 1,
    limit: 20,
    search: ''
  }

  sorts: Array<Record<string, any>> = [
    {
      command: 1,
      label: this.$i18n.t('kyc.sort.date'),
      divided: false,
      i18n: 'kyc.sort.date'
    },
    {
      command: 2,
      label: this.$i18n.t('kyc.sort.transaction'),
      divided: false,
      i18n: 'kyc.sort.transaction'
    },
    {
      command: 3,
      label: this.$i18n.t('kyc.sort.full-name'),
      divided: false,
      i18n: 'kyc.sort.full-name'
    }
  ]
  sortActive = 1
  listCountry: IListCountry[] = countryJson
  identificationType: Array<Record<string, any>> = [
    {
      id: 0,
      type: 'Id Card',
      value: 'ID_CARD'
    },
    {
      id: 1,
      type: 'Passport',
      value: 'PASSPORT'
    },
    {
      id: 2,
      type: 'Driver’s License',
      value: 'DRIVER_LICENSE'
    }
  ]
  isVisible = false

  @Watch('filter.search') handleSearch(value: string): void {
    this.searchText(value)
  }

  searchText = debounce((value: string) => {
    this.$emit('filter', {
      ...this.filter,
      search: trim(value)
    })
  }, 500)

  created(): void {
    EventBus.$on('changeLang', () => {
      console.log('a', window.localStorage.getItem('bc-lang'))
      forEach(this.sorts, elm => {
        elm.label = this.$i18n.t(elm.i18n)
      })
      this.$forceUpdate()
    })
    EventBus.$on('changeTabCustomer', this.handleChangeTab)
    this.$emit('filter', this.filter)
  }
  destroyed(): void {
    EventBus.$off('changeLang')
    EventBus.$off('changeTab')
  }

  handleShowPopper(): void {
    this.isVisible = true
  }

  resetFilter(): void {
    this.filter = {
      search: '',
      orderBy: 1,
      fromCreatedAt: '',
      toCreatedAt: '',
      nationality: '',
      identificationType: '',
      approvedBy: ''
    }
  }

  handleChangeTab(): void {
    this.sortActive = 1
    this.queryApprove = {
      page: 1,
      limit: 20,
      search: ''
    }

    if (this.filter.search) {
      this.resetFilter()
    } else {
      this.$emit('filter', { ...this.filter, orderBy: 1, fromCreatedAt: '', toCreatedAt: '', nationality: '', identificationType: '', approvedBy: '' })
      this.filter = {
        ...this.filter,
        orderBy: 1,
        fromCreatedAt: '',
        toCreatedAt: '',
        nationality: '',
        identificationType: '',
        approvedBy: ''
      }
    }
  }

  handleSort(command: number): void {
    this.sortActive = command
    this.filter.orderBy = command
    this.$emit('filter', this.filter)
  }

  handleApply(): void {
    this.$emit('filter', this.filter)
    this.isVisible = false
  }

  handleReset(): void {
    this.filter = {
      ...this.filter,
      fromCreatedAt: '',
      toCreatedAt: '',
      nationality: '',
      identificationType: '',
      approvedBy: ''
    }
    this.$emit('filter', this.filter)
    this.isVisible = false
  }
}
